import React from 'react';

const PersonFrom = ({ addPerson, newName, newNumber, handleNameChange, handleNumberChange }) => {
    return (
        <form onSubmit={addPerson}>
            <div>
                name: <input
                value={newName}
                onChange={handleNameChange}
            />
                <div>
                    number: <input
                    value={newNumber}
                    onChange={handleNumberChange}
                />
                </div>
            </div>
            <div>
                <button type="submit">add</button>
            </div>
        </form>
    )
}
export default PersonFrom;
